/*Animaciones*/
div.drop {
  animation: drop 1s;
  display: flex;
  align-items: center;
}
@keyframes drop {
  0% {
    transform: translate3d(0, -100px, 0);
    opacity: 0;
  }
  to {
    opacity: 1;
    transform: translate3d(0, 0px, 0);
  }
}
/*Animaciones*/
